// themespezifische Styles

// bannercomponents

.bannerComponent {

  $bannerComponentDefaultBgColor: $primary;
  $bannerComponentDefaultColor: $white;

  background-color: $bannerComponentDefaultBgColor;
  .labelWrapper,
  .headlineWrapper,
  .textWrapper,
  .btn-outline-primary {
    color: $bannerComponentDefaultColor;
  }
  .labelWrapper:after {
    background-color: $bannerComponentDefaultColor;
  }
  .btn-outline-primary {
    border: 1px solid $bannerComponentDefaultColor;
  }
  .btn-primary {
    color: $bannerComponentDefaultBgColor;
    background-color: $bannerComponentDefaultColor;
    border: 1px solid $bannerComponentDefaultBgColor;
  }
  .textWrapper-inner {
    background-color: $bannerComponentDefaultBgColor;
    border-bottom-left-radius: remCalc(4);
    border-bottom-right-radius: remCalc(4);
    @include media-breakpoint-up(lg) {
      border-bottom-left-radius: remCalc(0);
      border-bottom-right-radius: remCalc(0);
    }
  }
  .textWrapper-outer {
    background-color: $white;
    @include media-breakpoint-up(lg) {
      background-color: $bannerComponentDefaultBgColor;
    }
  }
  .headlineWrapper {
    @include media-breakpoint-up(md) {
      text-indent: remCalc(-40);
    }
  }
  img {
    height: auto;
    width: 100%;
  }
}

.herobanner {
  .textWrapper-inner {
    @include media-breakpoint-up(lg) {
      max-width: remCalc(567);
    }
  }
  picture {
    @include media-breakpoint-up(lg) {
      height: remCalc(383);
      width: remCalc(600);
    }
    @include media-breakpoint-up(xl) {
      height: remCalc(457);
      width: remCalc(712);
    }
    img {
      @include media-breakpoint-up(lg) {
        height: 100%;
        width: auto;
      }
    }
  }
}

