.teaserWrapperComponent {
  @include media-breakpoint-up(md) {
    overflow-x: auto;
  }
  overflow-x: auto;
  .teaserbanner {
    .textWrapper-outer {
      @include media-breakpoint-up(lg) {
        background-color: $white !important;
      }
    }
    .headlineWrapper {
      text-indent: 0;
    }
    .textWrapper-inner {
      @extend .mx-3;
      @extend .p-4;
      @include media-breakpoint-up(lg) {
        border-bottom-left-radius: remCalc(4);
        border-bottom-right-radius: remCalc(4);
      }
    }
  }
