$sideMenuWidth: 88vw;
$animationTime: .5s;

.sideMenuOpener {
  cursor: pointer;
}

.overlay {
  visibility: hidden;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  transition: opacity $animationTime;
  opacity: 0;

  &.active {
    visibility: visible;
    opacity: 0.3;
  }
}

body {
  transition: $animationTime;
  position: relative;
  left: 0;
}

// Main-Content mit Sidemenu verschieben
body.moveRight {
  left: $sideMenuWidth;
  overflow: hidden;
  // Scrolls body to top but prevents scrolling of body
  position: fixed;
}

.mobile-side-menu {
  visibility: hidden;
  width: $sideMenuWidth;
  position: fixed;
  top: 0;
  bottom: 0;
  left: -$sideMenuWidth;
  overflow: scroll;
  transition: $animationTime;

  // Verhindert Fehler im Crome bei touch-events
  touch-action: manipulation;

  &.active {
    visibility: visible;
    left: 0;
  }

  .sideMenuSlot {
    // Ermglicht es flex Containern kleiner als Parent zu werden
    min-height: 0;
  }

  .sideMenuCloser {
    position: absolute;
    top: $spacer * 0.5;
    right: $spacer * 0.5;
    z-index: 11;
  }

  .sideMenuOverlay {
    visibility: hidden;
    // Border bleibt sichtbar
    width: calc(#{$sideMenuWidth} - 0.25rem);
    height: 100vh;
    position: fixed;
    top: 0;
    left: -$sideMenuWidth;
    background-color: $white;
    transition: $animationTime;

    &.active {
      visibility: visible;
      left: 0;
    }

    .scrollable {
      overflow-y: auto;
    }

    .sideMenuOverlayHeadline {
      bottom: -1px;
    }
  }
}