// buttons

// sizing

.btn-small {
  @extend .display-25;
}

$icon-width: 24;
.btn.icon-svg {
  &::before {
    content: '';
    position: relative;
    display: inline-block;
    left: 0;
    top: 0;
    width: #{$icon-width}px;
    height: #{$icon-width}px;
  }
  &.icon-left {
    text-indent: -#{$icon-width}px;
    &::before {
      float: left;
    }
  }
  &.icon-right {
    text-indent: #{$icon-width}px;
    &::before {
      float: right;
    }
  }
}

