//  Helper function to replace characters in a string
@function str-replace($string, $search, $replace: '') {
  $index: str-index($string, $search);
  @return if($index,
          str-slice($string, 1, $index - 1) + $replace +
        str-replace(str-slice($string, $index +
        str-length($search)), $search, $replace),
          $string);
}





// textlinks (als Button, nicht inline)

.btn-link {
  color: $dark;
  text-decoration: underline;
  $fill-color: str-replace(#{$dark},'#', '%23');
  $icon-map: ();
  $icon-map: map-merge($icon-map, svgIconGenerator($fill-color) );
  @each $key, $bgImageUrl in $icon-map {
    $bgImageUrl: str-replace($bgImageUrl,'<', '%3C');
    $bgImageUrl: str-replace($bgImageUrl,'>', '%3E');
    &.icon-#{$key}::before {
      background-image: $bgImageUrl;
    }
  }
  &:hover {}
  &.disabled,
  &:disabled {
    color: $secondary;
    $fill-color: str-replace(#{$secondary},'#', '%23');
    $icon-map: ();
    $icon-map: map-merge($icon-map, svgIconGenerator($fill-color) );
    @each $key, $bgImageUrl in $icon-map {
      $bgImageUrl: str-replace($bgImageUrl,'<', '%3C');
      $bgImageUrl: str-replace($bgImageUrl,'>', '%3E');
      &.icon-#{$key}::before {
        background-image: $bgImageUrl;
      }
    }
  }
  &:not(:disabled):not(.disabled):active,
  &:not(:disabled):not(.disabled).active {}
}

// color variants
.btn-dark {
  color: $white;
  $fill-color: str-replace(#{$white},'#', '%23');
  $icon-map: ();
  $icon-map: map-merge($icon-map, svgIconGenerator($fill-color) );
  @each $key, $bgImageUrl in $icon-map {
    $bgImageUrl: str-replace($bgImageUrl,'<', '%3C');
    $bgImageUrl: str-replace($bgImageUrl,'>', '%3E');
    &.icon-#{$key}::before {
      background-image: $bgImageUrl;
    }
  }

  &:hover {
    background-color: $primary;
    border-color: $primary;
  }
  &.disabled,
  &:disabled {
    background-color: $gray;
    border-color: $gray;
    color: $secondary;
    $fill-color: str-replace(#{$secondary},'#', '%23');
    $icon-map: ();
    $icon-map: map-merge($icon-map, svgIconGenerator($fill-color) );
    @each $key, $bgImageUrl in $icon-map {
      $bgImageUrl: str-replace($bgImageUrl,'<', '%3C');
      $bgImageUrl: str-replace($bgImageUrl,'>', '%3E');
      &.icon-#{$key}::before {
        background-image: $bgImageUrl;
      }
    }

  }
  &:not(:disabled):not(.disabled):active,
  &:not(:disabled):not(.disabled).active {
    background-color: $red-coral-dark;
    border-color: $red-coral-dark;

    $fill-color: str-replace(#{$white},'#', '%23');
    $icon-map: ();
    $icon-map: map-merge($icon-map, svgIconGenerator($fill-color) );
    @each $key, $bgImageUrl in $icon-map {
      $bgImageUrl: str-replace($bgImageUrl,'<', '%3C');
      $bgImageUrl: str-replace($bgImageUrl,'>', '%3E');
      &.icon-#{$key}::before {
        background-image: $bgImageUrl;
      }
    }

  }
}

.btn-light {
  color: $secondary;
  &:hover {
    background-color: $gray;
    border-color: $gray;
    color: $dark;
  }
  &.disabled,
  &:disabled {
    color: $dark;
    background-color: #E7E7E7;
    border-color: #E7E7E7;
    opacity: 0.5;
  }
  &:not(:disabled):not(.disabled):active,
  &:not(:disabled):not(.disabled).active {
    color: $dark;
    background-color: #E7E7E7;
    border-color: #E7E7E7;
    opacity: 0.5;
  }
}

.btn-blank {
  color: $dark;
  $fill-color: $white;
  $icon-map: ();
  $icon-map: map-merge($icon-map, svgIconGenerator($fill-color) );
  @each $key, $bgImageUrl in $icon-map {
    $bgImageUrl: str-replace($bgImageUrl,'<', '%3C');
    $bgImageUrl: str-replace($bgImageUrl,'>', '%3E');
    $bgImageUrl: str-replace($bgImageUrl,'#', '%23');
    &.icon-#{$key}::before {
      background-image: $bgImageUrl;
    }
  }
  &:hover {
    color: $red-coral;
  }
  &.disabled,
  &:disabled {
    color: $dark;
    opacity: 0.5;
  }
  &:not(:disabled):not(.disabled):active,
  &:not(:disabled):not(.disabled).active {
    color: $dark;
    opacity: 0.5;
  }
}


