.category-bar-component {
  .link-wrapper {
    overflow-x: auto;
    overflow-y: hidden;
    .category-btn {
      height: 72px;
      line-height: 31px;
      white-space: pre-wrap;
      @include media-breakpoint-down(md) {
        min-width: 173px;
      }
      &:hover, &:active {
        background-color: $black;
        border-color: $white;
        color: $white;
      }
    }
  }
}

