// _type.scss

// map for breakpoint specific line-height
$display-map: (
        display-1: (
                line-height: 40,
                line-height-md: 64
        ),
        display-2: (
                line-height: 40,
                line-height-md: 56
        ),
        display-3: (
                line-height: 40,
                line-height-md: 48
        ),
        display-4:  (line-height: 32),
        display-5:  (line-height: 32),
        display-6:  (line-height: 28),
        display-7:  (line-height: 24),
        display-21:  (
                line-height: 26,
                line-height-lg: 26
        ),
        display-22:  (line-height: 24),
        display-23:  (line-height: 24),
        display-24:  (line-height: 20),
        display-25:  (line-height: 20),
        display-26:  (line-height: 14),
        display-27:  (line-height: 16),
        display-28:  (
                line-height: 40,
                line-height-lg: 32
        )
);

@function map-deep-get($display-map, $keys...) {
  @each $key in $keys {
    $display-map: map-get($display-map, $key);
  }
  @return $display-map;
}

////////////////////////////
/// Type display classes ///
////////////////////////////

////////////////////////// Headline classes  //////////////////////////////

.display-1, .display-2, .display-3, .display-4, .display-5 {
  font-family: $headings-font-family;
}

h1, h2, h3, h4, h5 {
  font-family: $font-family-base;
}

/* GT Super Display Bold, 32/40px */
.display-1, .display-2, .display-3, .display-4 {
  font-size: remCalc(32);
  line-height: remCalc(map-deep-get($display-map, 'display-1', 'line-height'));
  font-weight: 400;
}

/* GT Super Display Bold, 64/64px */
.display-1 {
  @include media-breakpoint-up(md) {
    font-size: remCalc(64);
    line-height: remCalc(map-deep-get($display-map, 'display-1', 'line-height-md'));
  }
}

/* GT Super Display Bold, 56/56px */
.display-2 {
  @include media-breakpoint-up(md) {
    font-size: remCalc(56);
    line-height: remCalc(map-deep-get($display-map, 'display-2', 'line-height-md'));
  }
}

/* GT Super Display Bold, 48/48px */
.display-3 {
  @include media-breakpoint-up(md) {
    font-size: remCalc(48);
    line-height: remCalc(map-deep-get($display-map, 'display-3', 'line-height-md'));
  }
}

/* GT Super Display Bold, 24/32px */
.display-5 {
  font-size: remCalc(24);
  line-height: remCalc(map-deep-get($display-map, 'display-5', 'line-height'));
  font-weight: 400;
}

/* Open Sans SemiBold, 20/28px */
.display-6 {
  font-size: remCalc(20);
  line-height: remCalc(map-deep-get($display-map, 'display-6', 'line-height'));
  font-weight: 600;
}

/* Open Sans SemiBold, 16/24px */
.display-7 {
  font-size: remCalc(16);
  line-height: remCalc(map-deep-get($display-map, 'display-7', 'line-height'));
  font-weight: 600;
}

////////////////////// Text classes ////////////////////////////

/* Open Sans Regular, 16/26px */
.display-21 {
  font-size: remCalc(16);
  line-height: remCalc(map-deep-get($display-map, 'display-21', 'line-height'));
  font-weight: 400;
}

/* Open Sans Regular 600, 14/24px */
.display-22 {
  font-size: remCalc(14);
  line-height: remCalc(map-deep-get($display-map, 'display-22', 'line-height'));
  font-weight: 600;
}

/* Open Sans Regular, 14/24px */
.display-23 {
  font-size: remCalc(14);
  line-height: remCalc(map-deep-get($display-map, 'display-23', 'line-height'));
  font-weight: 400;
}

/* Open Sans Regular 600, 12/20px */
.display-24 {
  font-size: remCalc(12);
  line-height: remCalc(map-deep-get($display-map, 'display-24', 'line-height'));
  font-weight: 600;
}

/* Open Sans Regular, 12/20px */
.display-25 {
  font-size: remCalc(12);
  line-height: remCalc(map-deep-get($display-map, 'display-25', 'line-height'));
  font-weight: 400;
}

/* Open Sans Regular, 10/14px */
.display-26 {
  font-size: remCalc(10);
  line-height: remCalc(map-deep-get($display-map, 'display-26', 'line-height'));
  font-weight: 400;
}

/* Open Sans Bold, 12/16px, 2px spacing */
.display-27 {
  font-size: remCalc(12);
  line-height: remCalc(map-deep-get($display-map, 'display-27', 'line-height'));
  font-weight: 700;
  letter-spacing: remCalc(2);
  text-transform: uppercase;
  &:after {
    content: "";
    width: 25px;
    height: 1px;
    background-color: $body-color;
    position: relative;
    left: 0;
    top: auto;
    bottom: 0;
    display: block;
    @extend .mt-2;
  }
}

/*
  Open Sans Regular, 14/40px
  Open Sans Regular, 14/32px
*/
.display-28 {
  font-size: remCalc(14);
  line-height: remCalc(map-deep-get($display-map, 'display-28', 'line-height'));
  font-weight: 400;
  color: $red;
  @include media-breakpoint-up(lg) {
    line-height: remCalc(map-deep-get($display-map, 'display-28', 'line-height-lg'));
  }
}

$display-map: (
        display-1: (
                line-height: 40,
                line-height-md: 64
        ),
        display-2: (
                line-height: 40,
                line-height-md: 58
        ),
        display-3: (
                line-height: 40,
                line-height-md: 48
        ),
        display-4:  (line-height: 32),
        display-5:  (line-height: 32),
        display-6:  (line-height: 28),
        display-7:  (line-height: 24),
        display-21:  (line-height: 26),
        display-22:  (line-height: 24),
        display-23:  (line-height: 24),
        display-24:  (line-height: 20),
        display-25:  (line-height: 20),
        display-26:  (line-height: 14),
        display-27:  (line-height: 16),
        display-28:  (
                line-height: 40,
                line-height-lg: 32
        )
);
@each $displayType, $map in $display-map {
  .#{$displayType} {
    @each $breakpoint in map-keys($grid-breakpoints) {
      $infix: breakpoint-infix($breakpoint, $grid-breakpoints);
      @if map-has-key($map, "line-height#{$infix}") {
        @for $i from 1 through 5 {
          @include media-breakpoint-up($breakpoint, $grid-breakpoints) {
            &.max-lines#{$infix}-#{$i} {
              overflow: hidden;
              max-height: remCalc(map-get($map, line-height#{$infix})*$i);
            }
          }
        }
      }
    }
  }
}
