// Funktion berechnet Pixel zu rem
@function remCalc($size) {
  $remSize: $size/16;
  @return #{$remSize}rem;
}

// themespezifische Variablen
// berschreibt bootstrap Variablen

// Color system

$gray-line:           #E7E7E7;
$gray-secondary:      #828282;
$gray-primary:        #191919;
$gray-surface:        #F2F2F2;
$white:               #FFF;
$red:                 #eb5050;
$red-velvet:          #FF7575;
$red-coral:           #FE8273;
$red-coral-dark:      #EA5E4D;
$green:               #5AC372;
$green-velvet:        #78E190;
$yellow-sunshine:     #FFCF4E;
$black:               #000 !default; // neccessary for box-shadow overrides below.

// theme colors
$primary:             $red-coral;
$secondary:           $gray-secondary;
$gray:                $gray-line;
$light:               $gray-surface;
$dark:                $gray-primary;

$theme-colors: (
        "gray": $gray,
        "red-velvet": $red-velvet,
        "red-coral-dark": $red-coral-dark,
        "green": $green,
        "green-velvet": $green-velvet,
        "yellow-sunshine":     $yellow-sunshine
);

// Fonts
//
// Font, line-height, and color for body text, headings, and more.

$font-family-base: "Open Sans", Arial, sans-serif;

$font-size-base:              1rem;
$font-size-lg:                $font-size-base;
$font-size-sm:                $font-size-base;

$font-weight-bold:            600;
$line-height-base:            1;

$h1-font-size: $font-size-base * 4;
$h2-font-size: $font-size-base * 2;
$h3-font-size: $font-size-base * 1.5;
$h4-font-size: $font-size-base * 1.25;
$h5-font-size: $font-size-base;
$h6-font-size: $font-size-base;


$headings-margin-bottom:      0;
$headings-font-family:        "GT Super Display Bold", serif;
$headings-color:              $dark;

$text-16:                     $font-size-base;
$text-14:                     $font-size-base * 0.875;
$text-12:                     $font-size-base * 0.75;
$text-10:                     $font-size-base * 0.625;

// Buttons + Forms
//
// Shared variables that are reassigned to `$input-` and `$btn-` specific variables.

$input-btn-padding-y:         0.5rem;
$input-btn-padding-x:         1rem;
$input-btn-font-size:         $text-14;
$input-btn-line-height:       1.5;

$input-btn-focus-box-shadow: none;

$input-btn-padding-y-sm:      .25rem !default;
$input-btn-padding-x-sm:      1rem;
$input-btn-font-size-sm:      0.75rem;
$input-btn-line-height-sm:    1.25;

$input-btn-padding-y-lg:      $input-btn-padding-y;
$input-btn-padding-x-lg:      $input-btn-padding-x;
$input-btn-font-size-lg:      1rem;
$input-btn-line-height-lg:    1.5;


// $input-btn-border-width:      0;

// Buttons
//
// For each of Bootstrap's buttons, define text, background, and border color.
$btn-line-height:             1.5rem;

$btn-font-weight: $font-weight-bold;
$btn-box-shadow: none;

$btn-focus-width:             0;

$btn-focus-box-shadow:        none;
$btn-disabled-opacity:        1;
$btn-active-box-shadow:       none;

$btn-link-disabled-color:     $secondary;

//$btn-block-spacing-y:         .5rem !default;

// Allows for customizing button radius independently from global border radius
$btn-border-radius:           remCalc(4);
$btn-border-radius-lg:        $btn-border-radius;
$btn-border-radius-sm:        $btn-border-radius;

$btn-transition:              color .15s ease-in-out, background-color .15s ease-in-out, border-color .15s ease-in-out, box-shadow .15s ease-in-out !default;

// Forms --> tbd

// Boxshadow
// expects $black color-variable above
$box-shadow-sm: 0 .5rem 1.875rem rgba($black, .06);
$box-shadow-lg: 0 .5rem 1.25rem rgba($black, .1);

$input-placeholder-color: $secondary;



