.btn-collapsible {
  color: $black;
  background: url('../../images/icon_minus.svg') $white no-repeat right;
  background-origin: content-box;

  &:focus {
    box-shadow: none;
  }

  &.collapsed {
    background-image: url('../../images/icon_plus.svg');
  }
}

.btn-arrow-right {
  background: url('../../images/icon_arrow_right.svg') $white no-repeat right;
  background-origin: content-box;
}

// Leider notwendig fr mobile Navigation
.btn-nav-primary-mobile a {
  @extend .btn;
  @extend .btn-block;
  @extend .font-weight-bold;
  @extend .border-light;
  @extend .border-top-0;
  @extend .border-left-0;
  @extend .border-right-0;
  @extend .rounded-0;
  @extend .py-2;
  @extend .pl-3;
  @extend .mt-0;
  @extend .text-left;
  white-space: inherit;
}

.btn-nav-secondary-mobile a {
  @extend .btn;
  @extend .btn-block;
  @extend .border-light;
  @extend .border-top-0;
  @extend .border-left-0;
  @extend .border-right-0;
  @extend .rounded-0;
  @extend .py-2;
  @extend .pl-5;
  @extend .mt-0;
  @extend .text-left;
  white-space: inherit;
}

.btn-nav-thirdly-mobile a {
  @extend .btn;
  @extend .btn-block;
  @extend .border-0;
  @extend .py-2;
  @extend .pl-5;
  @extend .mt-0;
  @extend .text-left;
  white-space: inherit;
}