.first-level-nav, .second-level-nav{
  a:hover {
    text-decoration: none;
  }
}

.first-level-nav {
  .nav-elem{
    &:hover {
      @extend .border-dark;
    }
  }
}

.second-level-nav {
  .nav-elem{
    &.active, &:hover {
      @extend .border-primary;
    }
  }

  nav {
    display: none;

    &.active {
      display: block;
    }
  }

  .navigation-flyout-opener {
    cursor: pointer;
  }
}

.second-level-nav-mobile {
  overflow-y: auto;
}

.navigation-flyout {
  position: absolute;
  width: 100%;
  height: remCalc(548);

  // Nicht in Bootstrap, da der Container zwei Border-Farben hat.
  border-top: $border-width solid $gray;
  border-bottom-color: $primary;

  .navigation-flyout-closer {
    cursor: pointer;
    position: absolute;
    top: 0;
    right: 0;
  }

  .navigation-list {
    margin-right: -0.75rem;
    margin-left: -0.75rem;
    overflow: hidden;

    .navigation-headline-wrapper {
      height: 0;
      position: relative;
      top: -3rem;
    }

    .navigation-headline, .navigation-link {
      margin-right: 0.75rem;
      margin-left: 0.75rem;
    }

    .navigation-linebreak {
      width: 0;
      height: 101%;
    }

    .navigation-link .navigation-image-text {
      height: 2.5rem;
    }
  }
}
