@mixin fillSvg($color) {
  path {
    fill: $color;
  }
}

@mixin strokeSvg($color) {
  path {
    stroke: $color;
  }
}

@each $color, $value in $theme-colors {
  .svg-#{$color} {
    @include fillSvg($value);
  }
  .svg-stroke-#{$color} {
    @include strokeSvg($value)
  }
}

.svg-white {
  @include fillSvg($white);
}
