// themespezifische Fonts
@font-face {
  font-family: 'GT Super Display Bold';
  src: url('../fonts/GT-Super-Display-Bold.eot'); /* IE9 Compat Modes */
  src: url('../fonts/GT-Super-Display-Bold.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
  url('../fonts/GT-Super-Display-Bold.woff2') format('woff2'), /* Super Modern Browsers */
  url('../fonts/GT-Super-Display-Bold.woff') format('woff'), /* Pretty Modern Browsers */
  url('../fonts/GT-Super-Display-Bold.ttf')  format('truetype') /* Safari, Android, iOS */
}
@import url('https://fonts.googleapis.com/css?family=Open+Sans:400,600,700');