.flyout-login,
.modal-close{
  z-index: 100;
  right:15px;
  margin-top: 1px;
  position: absolute;
  display: none;
}

.headerLogin-button{
  cursor: pointer;
  &.active{
    border-bottom: 1px solid $primary;
    margin: -1px 0;
  }
}

.flyout-close {
  cursor: pointer;
  position: absolute;
  top: $spacer * 0.5;
  right: $spacer * 0.5;
}

.icon-password-show{
  cursor: pointer;
  z-index: 2;
  position: absolute;
  top: 6px;
  right: 6px;
  line-height: 24px;

  .icon-show{
    display: block;
  }
  .icon-hide{
    display: none;
  }
   svg{
     path:nth-child(1){
       stroke: $secondary;
     }
     path:nth-child(2){
       fill: $secondary;
     }
   }
}

.userFormLogin-mobile {
  .btn-register,
  .btn-login{
    max-width: 280px;
  }
}

@include media-breakpoint-up(sm) {
  .modal-dialog {
    max-width: inherit;
  }
}

.modal-close {
  z-index: 2000;
  top: $spacer * 0.5;
  right: $spacer * 0.5;
  font-size: inherit;
}