// feste Bildgre
$cart-image-width-mobile: 94px;
$cart-image-width-desktop: 128px;

.cart-items {
  .cart-headline-link:hover {
    text-decoration: none;
  }

  .cart-amount {
    top: -0.75rem;
    left: -0.75rem;

    .cart-amount-number {
      top: 0;
      left: 0;
    }
  }

  .cart-image {
    width: $cart-image-width-mobile;
    img {
      width: 100%;
    }
  }
  .next-to-cart-image {
    //Abstand 1rem
    width: calc(100% - #{$cart-image-width-mobile} - 1rem);
  }

  @include media-breakpoint-up(md) {
    .cart-image {
      width: $cart-image-width-desktop;
    }
    .next-to-cart-image {
      width: calc(100% - #{$cart-image-width-desktop} - 1rem);
    }
  }
}

