.header-icon {
  .icon {
    .icon-hover {
      display: none;
    }

    .icon-overlay {
      position: absolute;
      top: -10px;
      right: -7px;

      .icon-overlay-number {
        position: absolute;
        width: 1rem;
        top: 6px;
        right: 0;
      }
    }
  }

  &:hover .icon {
    .icon-non-hover {
      display: none;
    }
    .icon-hover {
      display: block;
    }
  }

  a:hover {
    text-decoration: none;
  }
}

.icon-overlay-logged{
  position: absolute;
  right: 2px;
  top: -5px;
}

.bg-icon-mobile {
  width: 2.5rem;
  height: 2.5rem;
}